:root {
    --azul-corporativo: rgb(31, 81, 156);
    --azul-oscuro: #19274c;
    --azul-marino: #1a365d;
    --gris-elegante: rgb(64, 75, 105);
    --verde-confianza: rgb(34, 139, 34);
    --verde-lima: #7dd83f;
    --naranja-accion: rgb(255, 111, 67);
    --dorado-premium: rgb(212, 175, 55);
    --amarillo-calido: #f7b731;
    --azul-celeste: #3498db;
    --gris-claro: rgb(248, 249, 250);
    --blanco-puro: rgb(255, 255, 255);
    --blanco-azuloso: #f8fafc;
    --gris-texto: rgb(73, 80, 87);
  }

  .home-page * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .home-page {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    color: var(--gris-texto);
    background: var(--gris-claro);
  }

  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }

  /* Header */
  .header {
    background: var(--blanco-puro);
    box-shadow: 0 2px 15px rgba(31, 81, 156, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
    border-bottom: 1px solid rgba(31, 81, 156, 0.1);
  }

  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;
  }

  .logo {
    font-size: 2rem;
    font-weight: bold;
    color: var(--azul-corporativo);
    text-decoration: none;
    display: flex;
    align-items: center;
  }

  .logo-image {
    height: 50px;
    width: auto;
    max-width: 200px;
    object-fit: contain;
  }

  .nav-links {
    display: flex;
    list-style: none;
    gap: 2rem;
  }

  .nav-links a {
    color: var(--gris-elegante);
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
    padding: 0.5rem 1rem;
    border-radius: 6px;
  }

  .nav-links a:hover {
    background: var(--azul-corporativo);
    color: white;
    transform: translateY(-1px);
  }

  /* Hero Section */
  .hero {
    background: linear-gradient(135deg, var(--azul-corporativo), var(--azul-oscuro));
    color: white;
    padding: 8rem 0;
    text-align: center;
    position: relative;
    overflow: hidden;
  }

  .hero::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="threads" patternUnits="userSpaceOnUse" width="50" height="50"><path d="M0,25 Q25,0 50,25 Q25,50 0,25" stroke="rgba(255,255,255,0.1)" fill="none" stroke-width="2"/></pattern></defs><rect width="100%" height="100%" fill="url(%23threads)"/></svg>');
    opacity: 0.3;
  }

  .hero-content {
    position: relative;
    z-index: 2;
  }

  .hero h1 {
    font-size: 4rem;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    animation: fadeInUp 1s ease-out;
  }

  .hero p {
    font-size: 1.5rem;
    margin-bottom: 2rem;
    opacity: 0.9;
    animation: fadeInUp 1s ease-out 0.3s both;
  }

  .cta-button {
    display: inline-block;
    background: var(--naranja-accion);
    color: white;
    padding: 1rem 2.5rem;
    text-decoration: none;
    border-radius: 8px;
    font-weight: bold;
    font-size: 1.1rem;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(255, 111, 67, 0.3);
    animation: fadeInUp 1s ease-out 0.6s both;
  }

  .cta-button:hover {
    background: rgb(230, 90, 50);
    transform: translateY(-2px);
    box-shadow: 0 6px 25px rgba(255, 111, 67, 0.4);
  }

  /* Features Section */
  .features {
    padding: 6rem 0;
    background: var(--blanco-puro);
  }

  .section-title {
    text-align: center;
    font-size: 3rem;
    margin-bottom: 3rem;
    color: var(--azul-oscuro);
  }

  .features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 3rem;
    margin-top: 3rem;
  }

  .feature-card {
    background: var(--blanco-puro);
    padding: 3rem 2rem;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 4px 20px rgba(31, 81, 156, 0.08);
    transition: all 0.3s ease;
    border: 1px solid rgba(31, 81, 156, 0.1);
  }

  .feature-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 8px 30px rgba(31, 81, 156, 0.15);
    border-color: var(--azul-corporativo);
  }

  .feature-icon {
    width: 80px;
    height: 80px;
    margin: 0 auto 2rem;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
    background: var(--azul-corporativo);
  }

  .feature-card h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: var(--azul-oscuro);
  }

  .feature-card p {
    color: var(--gris-texto);
    line-height: 1.8;
  }

  /* Products Section */
  .products {
    padding: 6rem 0;
    background: var(--gris-claro);
  }

  .products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
    margin-top: 3rem;
  }

  .product-card {
    background: var(--blanco-puro);
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 20px rgba(31, 81, 156, 0.08);
    transition: all 0.3s ease;
    border: 1px solid rgba(31, 81, 156, 0.05);
  }

  .product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 30px rgba(31, 81, 156, 0.15);
  }

  .product-image {
    height: 250px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--azul-corporativo), var(--gris-elegante));
  }

  .product-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }

  .product-card:hover .product-image img {
    transform: scale(1.05);
  }

  .product-info {
    padding: 2rem;
  }

  .product-info h3 {
    font-size: 1.4rem;
    margin-bottom: 1.5rem;
    color: var(--azul-oscuro);
    text-align: center;
    font-weight: bold;
  }

  .product-specs {
    margin-bottom: 1.5rem;
  }

  .product-specs p {
    color: var(--gris-texto);
    margin-bottom: 0.8rem;
    font-size: 0.95rem;
    line-height: 1.6;
  }

  .product-specs strong {
    color: var(--azul-oscuro);
    font-weight: 600;
  }

  .product-quote-btn {
    display: block;
    font-size: 1.3rem;
    font-weight: bold;
    text-align: center;
    background: linear-gradient(135deg, var(--naranja-accion), #e06347);
    color: white;
    padding: 0.8rem 1.5rem;
    border-radius: 8px;
    transition: all 0.3s ease;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    position: relative;
    overflow: hidden;
  }

  .product-quote-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
    transition: left 0.5s;
  }

  .product-quote-btn:hover::before {
    left: 100%;
  }

  .product-quote-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 15px rgba(255, 111, 67, 0.3);
    background: linear-gradient(135deg, var(--dorado-premium), var(--amarillo-calido));
  }

  /* Partners Section */
  .partners {
    padding: 6rem 0;
    background: var(--blanco-puro);
  }

  .partners-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2.5rem;
    margin-top: 3rem;
  }

  .partner-card {
    background: var(--blanco-puro);
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 8px 25px rgba(31, 81, 156, 0.1);
    transition: all 0.3s ease;
    border: 1px solid rgba(31, 81, 156, 0.1);
    display: flex;
    flex-direction: column;
  }

  .partner-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 15px 40px rgba(31, 81, 156, 0.15);
    border-color: var(--azul-corporativo);
  }

  .partner-logo {
    height: 200px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--gris-claro), var(--blanco-azuloso));
  }

  .partner-logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }

  .partner-card:hover .partner-logo img {
    transform: scale(1.05);
  }

  .partner-info {
    padding: 2.5rem;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .partner-info h3 {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    color: var(--azul-oscuro);
    text-align: center;
    font-weight: bold;
    border-bottom: 2px solid var(--dorado-premium);
    padding-bottom: 1rem;
  }

  .partner-info p {
    color: var(--gris-texto);
    line-height: 1.8;
    text-align: justify;
    font-size: 1rem;
  }

  /* Contact Section */
  .contact {
    padding: 6rem 0;
    background: linear-gradient(135deg, var(--azul-marino), var(--azul-oscuro));
    color: white;
    position: relative;
    overflow: hidden;
  }

  .contact::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="contact-pattern" patternUnits="userSpaceOnUse" width="60" height="60"><circle cx="30" cy="30" r="2" fill="rgba(255,255,255,0.1)"/></pattern></defs><rect width="100%" height="100%" fill="url(%23contact-pattern)"/></svg>');
    opacity: 0.5;
  }

  .contact .container {
    position: relative;
    z-index: 2;
  }

  .contact-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: start;
  }

  .contact-info {
    background: rgba(255, 255, 255, 0.1);
    padding: 3rem;
    border-radius: 16px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  }

  .contact-info h3 {
    font-size: 2.2rem;
    margin-bottom: 2rem;
    color: var(--dorado-premium);
    text-align: center;
    border-bottom: 2px solid var(--dorado-premium);
    padding-bottom: 1rem;
  }

  .contact-item {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
    font-size: 1.1rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    transition: all 0.3s ease;
  }

  .contact-item:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateX(5px);
  }

  .contact-item i {
    margin-right: 1.5rem;
    font-size: 1.8rem;
    color: var(--dorado-premium);
    width: 40px;
    text-align: center;
  }

  .contact-form {
    background: rgba(255, 255, 255, 0.95);
    padding: 3rem;
    border-radius: 16px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }

  .contact-form:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  }

  .form-group {
    margin-bottom: 1.5rem;
  }

  .form-group label {
    display: block;
    margin-bottom: 0.8rem;
    color: var(--azul-oscuro);
    font-weight: 600;
    font-size: 1.1rem;
  }

  .form-group input,
  .form-group textarea,
  .form-group select {
    width: 100%;
    padding: 1.2rem;
    border: 2px solid rgba(31, 81, 156, 0.2);
    border-radius: 12px;
    background: var(--blanco-puro);
    font-size: 1rem;
    color: var(--gris-texto);
    transition: all 0.3s ease;
    font-family: inherit;
  }

  .form-group input:focus,
  .form-group textarea:focus,
  .form-group select:focus {
    outline: none;
    border-color: var(--azul-corporativo);
    box-shadow: 0 0 0 3px rgba(31, 81, 156, 0.1);
    transform: translateY(-2px);
  }

  .form-group input::placeholder,
  .form-group textarea::placeholder {
    color: #999;
    font-style: italic;
  }

  .form-group textarea {
    height: 140px;
    resize: vertical;
    min-height: 100px;
    max-height: 300px;
  }

  .form-group select {
    cursor: pointer;
    appearance: none;
    background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="12" height="8" viewBox="0 0 12 8"><path fill="%23495057" d="M6 8L0 0h12z"/></svg>');
    background-repeat: no-repeat;
    background-position: right 1rem center;
    background-size: 12px;
    padding-right: 3rem;
  }

  .submit-btn {
    background: linear-gradient(135deg, var(--azul-corporativo), var(--azul-oscuro));
    color: white;
    padding: 1.2rem 2.5rem;
    border: none;
    border-radius: 12px;
    font-weight: bold;
    font-size: 1.1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 1px;
    position: relative;
    overflow: hidden;
  }

  .submit-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
    transition: left 0.5s;
  }

  .submit-btn:hover::before {
    left: 100%;
  }

  .submit-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 10px 25px rgba(31, 81, 156, 0.3);
    background: linear-gradient(135deg, var(--naranja-accion), #e06347);
  }

  /* Footer compacto y elegante */
  .footer {
    background: linear-gradient(135deg, var(--azul-oscuro), var(--azul-marino));
    color: white;
    padding: 2.5rem 0 1rem;
    margin-top: 2rem;
    position: relative;
    overflow: hidden;
  }

  .footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="footerPattern" patternUnits="userSpaceOnUse" width="40" height="40"><path d="M0,20 Q20,0 40,20 Q20,40 0,20" stroke="rgba(255,255,255,0.05)" fill="none" stroke-width="1"/></pattern></defs><rect width="100%" height="100%" fill="url(%23footerPattern)"/></svg>');
    opacity: 0.3;
  }

  .footer .container {
    position: relative;
    z-index: 2;
  }

  .footer-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-bottom: 2rem;
  }

  .footer-section h4 {
    color: var(--dorado-premium);
    font-size: 1.1rem;
    margin-bottom: 1rem;
    font-weight: 700;
    position: relative;
  }

  .footer-section h4::after {
    content: '';
    position: absolute;
    bottom: -0.3rem;
    left: 0;
    width: 30px;
    height: 2px;
    background: var(--dorado-premium);
    border-radius: 2px;
  }

  .footer-logo-img {
    height: 45px;
    width: auto;
    max-width: 180px;
    object-fit: contain;
    margin-bottom: 0.8rem;
  }

  .footer-description {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    line-height: 1.5;
    margin-bottom: 1.2rem;
  }

  .footer-social {
    display: flex;
    gap: 0.8rem;
  }

  .social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 50%;
    text-decoration: none;
    color: white;
    font-size: 1.3rem;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
  }

  .social-link:hover {
    background: var(--dorado-premium);
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(212, 175, 55, 0.4);
  }

  .footer-links-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
  }

  .footer-links-grid a {
    color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
    font-size: 0.9rem;
    transition: all 0.3s ease;
    padding: 0.2rem 0;
  }

  .footer-links-grid a:hover {
    color: var(--dorado-premium);
    padding-left: 0.3rem;
  }

  .footer-contact-compact {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
  }

  .footer-contact-compact p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    margin: 0;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }

  .footer-contact-compact a {
    color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
    transition: color 0.3s ease;
  }

  .footer-contact-compact a:hover {
    color: var(--dorado-premium);
  }

  .footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
    padding: 1.5rem 0 0.5rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
  }

  .footer-copyright p {
    margin: 0;
    color: rgba(255, 255, 255, 0.9);
    font-size: 0.9rem;
  }

  .footer-legal {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    flex-wrap: wrap;
  }

  .legal-link {
    color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
    font-size: 0.85rem;
    transition: color 0.3s ease;
  }

  .legal-link:hover {
    color: var(--dorado-premium);
  }

  .separator {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.8rem;
  }

  /* Botón de WhatsApp flotante */
  .whatsapp-btn {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 60px;
    height: 60px;
    background: #25D366;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    text-decoration: none;
    box-shadow: 0 4px 20px rgba(37, 211, 102, 0.3);
    transition: all 0.3s ease;
    z-index: 1000;
    animation: pulse 2s infinite;
  }

  .whatsapp-btn:hover {
    background: #128C7E;
    transform: scale(1.1);
    box-shadow: 0 6px 25px rgba(37, 211, 102, 0.5);
  }

  .whatsapp-btn svg {
    width: 28px;
    height: 28px;
  }

  @keyframes pulse {
    0% {
      box-shadow: 0 4px 20px rgba(37, 211, 102, 0.3);
    }
    50% {
      box-shadow: 0 4px 20px rgba(37, 211, 102, 0.6), 0 0 0 10px rgba(37, 211, 102, 0.1);
    }
    100% {
      box-shadow: 0 4px 20px rgba(37, 211, 102, 0.3);
    }
  }

  /* Animations */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes shake {
    0%, 100% { transform: translateX(0); }
    25% { transform: translateX(-5px); }
    75% { transform: translateX(5px); }
  }

  @keyframes slideIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes slideOut {
    from {
      opacity: 1;
      transform: translateY(0);
    }
    to {
      opacity: 0;
      transform: translateY(-20px);
    }
  }

  @keyframes bounceIn {
    0% {
      opacity: 0;
      transform: scale(0.3);
    }
    50% {
      opacity: 1;
      transform: scale(1.05);
    }
    70% {
      transform: scale(0.9);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }

  /* Estilos adicionales para validación de formulario */
  .error-message {
    animation: shake 0.3s ease-in-out;
  }

  .char-counter {
    transition: color 0.3s ease;
  }

  .success-message {
    animation: slideIn 0.5s ease-out;
  }

  /* Focus mejorado para accesibilidad */
  .form-group input:focus,
  .form-group textarea:focus,
  .form-group select:focus {
    outline: 2px solid var(--azul-corporativo);
    outline-offset: 2px;
  }

  /* Loading spinner para el botón de envío */
  .submit-btn.loading::after {
    content: '';
    width: 20px;
    height: 20px;
    border: 2px solid transparent;
    border-top: 2px solid white;
    border-radius: 50%;
    display: inline-block;
    animation: spin 1s linear infinite;
    margin-left: 10px;
  }

  @keyframes spin {
