    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="docs" patternUnits="userSpaceOnUse" width="20" height="20"><circle cx="10" cy="10" r="1" fill="rgba(31,81,156,0.05)"/></pattern></defs><rect width="100%" height="100%" fill="url(%23docs)"/></svg>');
    opacity: 0.6;
  }

