    border: 2px solid transparent;
    position: relative;
    overflow: hidden;
  }

  .document-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(31, 81, 156, 0.1), transparent);
    transition: left 0.5s ease;
  }

  .document-card:hover::before {
    left: 100%;
  }

  .document-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 16px 48px rgba(31, 81, 156, 0.15);
    border-color: var(--azul-corporativo);
  }

