  position: relative;
}

.applications::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="weave" patternUnits="userSpaceOnUse" width="40" height="40"><rect width="20" height="20" fill="rgba(31,81,156,0.03)"/><rect x="20" y="20" width="20" height="20" fill="rgba(31,81,156,0.03)"/></pattern></defs><rect width="100%" height="100%" fill="url(%23weave)"/></svg>');
  opacity: 0.8;
}

.applications .container {
